<template>
  <div class="add_new_app">
    <CContentHeader content="新建IOS应用" :isShowIcon="true" />

    <div class="ana-cover-wrap" v-if="!isHavaApp">
      <uploadApp @appFile="uploadFile" type="ipa" />
    </div>

    <div class="ana_content" v-if="isHavaApp">
      <div class="ana_content_wrap">
        <AppInfo :inputmodel="uploadInfo.analysis_con" />
        <AppVersion :appinfo="uploadInfo.analysis_con" />
        <viewRange @allow_ranges="allow_ranges" />
        <AppPreviewImg :previews="uploadInfo.previews" />
        <selectLocation :radio="uploadInfo" />
        <AppAddBtn @add="addBtn" />

      </div>
    </div>
    <!-- 加载中……lodding框 -->
    <div class="ana_loadding">

    </div>
  </div>
</template>
<script>
import AppInfo from "./components/appInfo.vue";
import AppVersion from "./components/appVersion";
import AppPreviewImg from "./components/appPreviewImg";
import AppAddBtn from "./components/appCreateBtn";
import uploadApp from "./components/uploadApp";
import selectLocation from './components/selectLocation';
import viewRange from './components/viewRange'

import AppInfoParser from "app-info-parser";

// 公有方法合并
import myMixinFn from './mixinFn'
import { setTimeout } from 'timers';
export default {
  mixins: [myMixinFn],
  data() {
    return {
      isHavaApp: false,
      appFile: null, // app文件
      appInfoParser: {}, // 解析出来的appInfoParse

      // 要上传的信息 以及一些中间结果 传递给子组件让子组件动态改变值
      uploadInfo: {
        analysis_con: {},
        // appName: "", // 应用名称
        // appDes: "", // 应用介绍
        previews: [], // 预览图片
        typeid: '', // typeid 应用分类 (场外 户内)
        allow_ranges: [], // 允许的
      },

      // 上传过程中产生的一些网络地址字符串
      middleFilesResult: {
        // appFileUrl: "", // 应用url地址
        storage_url: "", // 应用存储服务名
        source_name: "", // 应用源文件名
        logoUrl: '', // logoURL地址
        previews: [], // 预览图片地址
      }
    };
  },

  methods: {
    allow_ranges(arrAllow) {
      console.log('这里拿到选择的东西');
      console.log(arrAllow);
      // 修改允许的
      let item;
      for (let i = 0, length = arrAllow.length; i < length; i++) {
        item = arrAllow[i];
        let pushObj = {};
        if (item.type === 'dep') {
          pushObj.type = 'dep';
          pushObj.dep = item._id;
        } else if (item.type === 'user') {
          pushObj.type = 'user';
          pushObj.user = item._id;
        } else {
          // 都不满足则为 企业集团
          pushObj.type = 'dep';
          pushObj.dep = item._id;
        }
        pushObj.name = item.name;
        this.uploadInfo.allow_ranges.push(pushObj);
      }
    },

    // 选择本地应用包之后
    async uploadFile(files) {
      console.log("上传应用包之后");
      console.log(files);
      this.appFile = files;
      const parser = new AppInfoParser(files[0]);
      const result = await parser.parse();
      console.log("分析结果");
      console.log(result);
      console.log('打印json');

      console.log(JSON.stringify(result));
      this.appInfoParser = result;

      // 一些解析出的信息
      // this.uploadInfo.name = result["package"] || result["CFBundleIdentifier"] || result["CFBundleName"];
      this.uploadInfo.version = result["versionName"] || result["CFBundleShortVersionString"];
      this.uploadInfo.size = (this.appFile[0].size / 1024 / 1024).toFixed(2);
      // this.uploadInfo.build = result.CFBundleVersion;
      this.uploadInfo.icon = result.icon;
      this.uploadInfo.analysis_con = {
        packageName: result["package"] || result["CFBundleIdentifier"] || result["CFBundleName"],
        appName: result["CFBundleDisplayName"],
        version: result["versionName"] || result["CFBundleShortVersionString"],
        icon: result.icon,
        size: (this.appFile[0].size / 1024 / 1024).toFixed(2),
        build: result.CFBundleVersion
      }

      console.log('解析包内容');
      console.log(this.uploadInfo);

      this.isHavaApp = true;
    },

    // 上传logo
    async uploadLogo() {
      this.loadding = this.$zero.wholeLoadding({ text: '上传logo中...' });
      let logoUrl;
      if (this.uploadInfo.analysis_con.isBase64 === undefined) {
        logoUrl = await this.mixinBase64toUrl(this.uploadInfo.analysis_con.icon);
      } else {
        logoUrl = await this.mixinUploadImg([this.uploadInfo.analysis_con.imgFile]);
      }
      this.loadding.close();
      if (!logoUrl) {
        return console.log('logo上传出错了');
      }

      console.log('url路径地址');
      console.log(logoUrl);

      this.middleFilesResult.logoUrl = logoUrl[0].server_name;
      // logo上传完成 上传应用的所有的一些信息
      this.createApp();
    },

    // 上传预览图
    async uploadPreviewImg() {
      console.log('获取到的图片列表');
      console.log(this.uploadInfo.previews.files);
      try {
        if (!(this.uploadInfo.previews.files && this.uploadInfo.previews.files.length !== 0)) {
          // 如果不存在 或者 这个的长度为0 直接退出
          this.$zero.ToastWarning('没有预览图片');
          // 上传logo
          this.uploadLogo();
          return;
        }
        // console.log('object')
        this.loadding = this.$zero.wholeLoadding({ text: '上传预览图中..' });
        let uploadResult = await this.mixinUploadImg(this.uploadInfo.previews.files);
        this.loadding.close();

        // 上传logo
        this.uploadLogo();

        console.log('上传预览图结果');
        console.log(uploadResult);
        if (!uploadResult) {
          return;
        }

        for (let i = 0; i < uploadResult.length; i++) {
          this.middleFilesResult.previews.push(uploadResult[i].server_name);
        }
        // this.middleFilesResult.previews = 
      } catch (e) {
        console.log('执行过程出错了');
        console.log(e);
      }
    },

    // 创建app应用
    async createApp() {
      console.log('创建app应用');
      console.log('上传的什么东西');
      console.log(this.uploadInfo);
      console.log('应用的链接');
      console.log(this.middleFilesResult);

      // 此时肯定有这个东西了 之前判断很多次了
      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      console.log('创建应用接口调用');
      // 此刻全部上传完成 调取上传应用的接口
      this.loadding = this.$zero.wholeLoadding({ text: '上传应用信息...' });
      console.log('上传应用包信息');
      
      

      let uploadResult = await this.$api.application.createIOS({
        company_id: company_id,
        _name: this.uploadInfo.analysis_con.appName,
        typeid: this.uploadInfo.typeid,
        icon: this.middleFilesResult.logoUrl,
        description: this.uploadInfo.analysis_con.appDes,
        founder: user_id,
        allow_ranges: this.uploadInfo.allow_ranges,
        manages: [user_id],
        agentid: this.uploadInfo.analysis_con.packageName,
        build: 'build ' + this.uploadInfo.analysis_con.build,
        storage_url: this.middleFilesResult.storage_url, // 应用存储服务名
        source_name: this.middleFilesResult.source_name, // 应用源文件名
        version: this.uploadInfo.analysis_con.version,
        size: this.uploadInfo.analysis_con.size + 'M',
        previews: this.middleFilesResult.previews,
        // update_description: obj.update_description,
        package_type: ''
        // package_type: obj.package_type
      });
      this.loadding.close();

      console.log('上传结果');
      console.log(uploadResult);
      if (uploadResult.statusCode === 75500) {
        return this.$zero.notify({
          title: '应用创建失败',
          message: `服务繁忙，上传失败，请稍后再试`,
          duration: 0
        });
      }
      if (uploadResult.statusCode !== 75200 && uploadResult.statusCode !== 75201) {
        return this.$zero.notify({
          title: '应用创建失败',
          message: `应用创建失败，错误码:${uploadResult.statusCode}`,
          duration: 0
        });
      }

      // 新建成功
      this.$zero.notify({
        title: '应用创建成功',
        message: '新建了一个Ios应用，可在控制台查看',
        duration: 0
      });
      setTimeout(() => {
        // this.$router.push('/');
        // 返回上一层
        this.$router.go(-1);
      }, 2000);
    },

    // 点击了创建应用按钮
    async addBtn() {
      console.log("点击了创建应用按钮");
      /**
       * 点击了创建的流程，后面可等待优化
       *  1. 先去单独的上传应用包 -----> 换取应用包的网络链接地址
       *  2. 上传预览图 ----> 换取预览连接地址
       *  3. 上传base64位logo ---> 换取logo地址
       *  4. 拿到上述三个东西之后，再拿去已有的信息，上传，所有信息，创建成功
       */

      // 在这里统筹全局
      try {
        // 通过文件换取url地址
        this.loadding = this.$zero.wholeLoadding({ text: '上传应用包中...' });
        let uploadAppResult = await this.mixinUploadApp2url(this.appFile[0]);
        this.loadding.close();
        if (!uploadAppResult) {
          // 如果不存在则执行出错了
          return console.log('上传文件就出错了！');
        }

      //   {
      //     "upload_name": "c24e52a2-8129-44cf-ba61-98e7a855cbb9.ipa", 上传的源文件名名
      //     "server_name": "dec31ac0-c4af-11e9-8686-75466612dca2.ipa", 服务存储的文件名
      //     "format": "ipa",
      //     "statusCode": 75200
      // }

        // 结果存下来
        this.middleFilesResult.storage_url = uploadAppResult.server_name;
        this.middleFilesResult.source_name = uploadAppResult.upload_name;
        // 上传完成应用包之后 上传预览
        await this.uploadPreviewImg();

      } catch (e) {
        console.log('执行过程出错了');
        console.log(e);
      }
    },

  },

  created() {
    // 从Ios选择应用包来的
    if (this.$route.params.file) {
      this.uploadFile(this.$route.params.file);
    }
  },
  components: {
    AppInfo,
    AppVersion,
    AppPreviewImg,
    AppAddBtn,
    uploadApp,
    selectLocation,
    viewRange
  }
};
</script>
<style>
.add_new_app {
  height: calc(100% - 51px);
}
.ana_content {
  margin: 20px 35px;
}
.ana_content_wrap {
  padding: 0 40px;
  background: #fff;
}
.ana-cover-wrap {
  width: 100%;
  height: 100%;
}
</style>
